<template>
  <div class="content-box" :key="key">
    <el-steps :active="active" simple finish-status="success">
      <el-step title="生成答题卡"></el-step>
      <el-step title="导出答题卡"></el-step>
      <el-step title="扫描答题卡"></el-step>
    </el-steps>
    <div class="step-content">
      <div class="content-box">
        <!--生成答题卡-->
        <template v-if="active === 0">
          <!--<div>生成答题卡</div>-->
          <div class="generate-box" id="generate-suspend">
            <el-button type="primary" size="small"
                       @click="openSuspend($event)">打开悬浮层</el-button>
            <div class="suspend-box" id="suspend-id1">
              <span>我是悬浮层</span>
            </div>
          </div>
        </template>
        <!--导出答题卡-->
        <template v-if="active === 1">
          <div>导出答题卡</div>
        </template>
        <!--扫描答题卡-->
        <template v-if="active === 2">
          <div>扫描答题卡</div>
        </template>
      </div>
      <div class="operate-box">
        <el-button size="small" @click="preStep" :disabled="isPreDisable"
                   type="primary">上一步</el-button>
        <template v-if="active === 0">
          <el-button size="small" type="primary">生成答题卡</el-button>
        </template>
        <template v-if="active === 1">
          <el-button size="small" type="primary">导出答题卡</el-button>
        </template>
        <template v-if="active === 2">
          <el-button size="small" type="primary">开始扫描</el-button>
        </template>
        <el-button size="small" @click="nextStep" :disabled="isNextDisable"
                   type="primary">下一步</el-button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  // 步骤条使用
  name: 'CustomSteps',
  data() {
    return {
      key: new Date().getTime(),
      active: 0,
      isPreDisable: true,
      isNextDisable: false,
      sheetStyleList: [
        {
          code: 'A4',
          value: 'A4',
        },
        {
          code: 'A3',
          value: 'A3',
        },
      ],
      sheetStyle: 'A4',
    };
  },
  watch: {
    active() {
      this.isPreDisable = false;
      this.isNextDisable = false;
      if (this.active === 0) {
        this.isPreDisable = true;
      } else if (this.active === 2) {
        this.isNextDisable = true;
      }
    },
  },
  mounted() {
    document.onclick = (event) => {
      const suspend = document.getElementById('suspend-id1');
      if (suspend && !suspend.contains(event.target)) {
        suspend.style.display = 'none';
      }
    };
  },
  methods: {
    /**
     * 上一步
     */
    preStep() {
      this.active--;
    },
    /**
     * 下一步
     */
    nextStep() {
      this.active++;
    },
    /**
     * 打开悬浮层
     */
    openSuspend(event) {
      event.stopPropagation();
      let dom = null;
      dom = document.getElementById('suspend-id1');
      const currDisplay = dom.style.display;
      if (currDisplay === '' || currDisplay === 'none') {
        dom.style.display = 'inline-block';
      } else {
        dom.style.display = 'none';
      }
    },
  },
};
</script>

<style scoped lang="less">
.top-margin {
  margin-top: 12px;
}
.content-box {
  margin: 0 200px;
  border: 1px solid #e5e5e5;
  height: auto;
  text-align: center;
  .step-content {
    margin-top: 20px;
    .content-box {
      border: 0;
      .generate-box {
        position: relative;
        .suspend-box {
          display: none;
          transition: display 1s;
          z-index: 999;
          position: absolute;
          top: 40px;
          left: 0;
          width: 100%;
          padding: 20px;
          border: 1px solid #ccc;
          border-radius: 5px;
          box-shadow: 0 4px 16px 0 rgba(2, 44, 78, 0.08);
          background: #fff;

        }
      }
    }
    .operate-box {
      margin: 20px 0;
    }
  }
}
</style>
