import Vue from 'vue';
import Router from 'vue-router';
import MainIndex from '../views/MainIndex';
import ViewHome from '../views/ViewHome.vue';
import ViewSteps from '../views/ViewSteps';
import ViewPagination from '../views/ViewPagination';
import ViewTableWidth from '../views/ViewTableWidth';
import ViewValidator from '../views/ViewValidator';
import ViewTreeGrid from '../views/ViewTreeGrid';
import ViewCssShape from '../views/ViewCssShape';
import ViewPrizeDraw from '../views/ViewPrizeDraw';
import ViewImgDrag from '../views/ViewImgDrag';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/mainIndex',
    },
    {
      path: '/mainIndex',
      name: 'MainIndex',
      component: MainIndex,
      redirect: '/viewHome',
      children: [
        {
          path: '/viewHome',
          name: 'ViewHome',
          component: ViewHome,
        },
        {
          path: '/viewSteps',
          name: 'ViewSteps',
          component: ViewSteps,
        },
        {
          path: '/viewPagination',
          name: 'ViewPagination',
          component: ViewPagination,
        },
        {
          path: '/viewTableWidth',
          name: 'ViewTableWidth',
          component: ViewTableWidth,
        },
        {
          path: '/viewValidator',
          name: 'ViewValidator',
          component: ViewValidator,
        },
        {
          path: '/viewTreeGrid',
          name: 'ViewTreeGrid',
          component: ViewTreeGrid,
        },
        {
          path: '/viewCssShape',
          name: 'ViewCssShape',
          component: ViewCssShape,
        },
        {
          path: '/viewPrizeDraw',
          name: 'ViewPrizeDraw',
          component: ViewPrizeDraw,
        },
        {
          path: '/viewImgDrag',
          name: 'ViewImgDrag',
          component: ViewImgDrag,
        },
      ],
    },
  ],
});
