<template>
  <div class="content-box" :key="key">
    <el-form ref="form" :model="form" :rules="rules" label-width="150px" size="small">
      <el-form-item label="输入框" prop="vInput" ref="inputRef">
        <el-input v-model="form.vInput" placeholder="请输入内容"></el-input>
      </el-form-item>
      <el-form-item label="输入框(大区域)" prop="vTextArea" ref="textAreaRef">
        <el-input v-model="form.vTextArea" placeholder="请输入内容" type="textarea"
                  :autosize="{ minRows: 2, maxRows: 4}" maxlength="200"></el-input>
        <div class="text-area-tip">{{form.textAreaLength}}/200</div>
      </el-form-item>
      <el-form-item label="多选框" prop="vCheckBox">
        <el-checkbox v-model="form.vCheckBox" @change="checkChange">多选框</el-checkbox>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="submitForm('form')" :loading="submitLoad">{{submitText}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'validator',
  data() {
    const nameValidator = (rule, value, callback) => {
      console.log(rule);
      let tipText = '';
      const field = rule.field;
      if (field) {
        switch (field) {
        case 'vInput':
          tipText = '输入框内容';
          break;
        case 'vTextArea':
          tipText = '输入框(大区域)内容';
          break;
        }
      }
      if (!value.trim()) {
        return callback(new Error(`请输入${tipText}`));
      }
      callback();
    };
    return {
      key: new Date().getTime(),
      submitLoad: false, // 提交按钮是否显示loading
      submitText: '提交', // 提交按钮文本
      form: {
        vInput: '',
        vTextArea: '',
        textAreaLength: 0,
        vCheckBox: '',
      },
      rules: {
        vInput: [
          { required: true, validator: nameValidator, trigger: 'blur', },
        ],
        vTextArea: [
          { required: true, validator: nameValidator, trigger: 'blur', },
        ],
      },
    };
  },
  methods: {
    checkChange(val) {
      console.log(val);
    },
    /**
     * 设置提交按钮的一些信息
     * @param submitLoad 提交按钮是否loading
     * @param submitText 提交按钮文本信息
     */
    setSubmitBtnInfo(submitLoad, submitText) {
      this.submitLoad = submitLoad;
      this.submitText = submitText;
    },
    /**
     * 提交
     * @param formData
     */
    submitForm(formData) {
      this.setSubmitBtnInfo(true, '提交中');
      this.$refs[formData].validate((valid) => {
        const inputRef = this.$refs.inputRef;
        const status = inputRef.validateState;
        if (valid) {
          console.log(status);
          // 模拟调用接口提交数据
          setTimeout(() => {
            this.setSubmitBtnInfo(false, '提交');
          }, 3000);
        } else {
          this.setSubmitBtnInfo(false, '提交');
        }
      });
    },
  },
  watch: {
    'form.vTextArea': function watchTextArea() {
      // 监听文本长度
      this.form.textAreaLength = this.form.vTextArea.length;
    },
  },
};
</script>

<style scoped lang="less">
.content-box {
  width: 700px;
  margin: 0 auto;
  .text-area-tip {
    /*text-align: right;*/
    position: absolute;
    top: 100%;
    right: 0;
    line-height: 1;
    padding-top: 2px;
  }
}
</style>
