<template>
  <div class="content-box" :key="key">
    <define-pagination :current="currentInput" :pageNo="pageOpt.pageNo"
                       :pageSize="pageOpt.pageSize" :total="pageOpt.total"
                       :totalPageCount="pageOpt.totalPageCount"
                       @currentChange="handleCurrentChange"></define-pagination>
  </div>
</template>

<script>

import definePagination from '@/components/DefinePagination';

export default {
  // 自定义分页使用
  name: 'CustomPagination',
  components: {
    definePagination,
  },
  data() {
    return {
      key: new Date().getTime(),
      currentInput: '', // 当前页码
      pageOpt: {
        total: 40,
        totalPageCount: 4,
        pageNo: 1,
        pageSize: 10,
      },
    };
  },
  methods: {
    /**
     * 切换当前页
     * @param val 当前页码
     */
    handleCurrentChange(val) {
      this.pageOpt.pageNo = val;
      console.log(val);
    },
  },
};
</script>

<style scoped lang="less">
.content-box {
  text-align: center;
  .page-custom {
    display: inline-block;
    .page-jump-box {
      display: inline-block;
      margin-left: 24px;
      .input-box {
        margin: 0 2px;
        width: 50px;
        display: inline-block;
      }
    }
  }
}
</style>
<style lang="less">
.page-custom {
  span {
    font-weight: 400;
  }
  .page-jump-box {
    .input-box {
      input {
        text-align: center;
        height: 28px;
        line-height: 28px;
      }
    }
  }
}
</style>
