import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    sliderIsCollapse: false,
  },
  mutations: {
    /**
     * 修改侧边栏展开收缩
     * @param state 当前vuex对象
     */
    changeSliderCollapse(state) {
      state.sliderIsCollapse = !state.sliderIsCollapse;
    },
  },
  actions: {
  },
});
