<template>
<div class="content-box" :key="key">
  <el-table :data="tableData" size="small" border style="width: 100%;">
    <el-table-column label="单位名称" header-align="center" prop="name"
                     :show-overflow-tooltip="true" align="center" min-width="200"></el-table-column>

    <el-table-column label="地址" header-align="center" prop="address"
                     :show-overflow-tooltip="true" align="center" min-width="200"></el-table-column>

    <el-table-column label="电话" header-align="center" prop="telephone"
                     :show-overflow-tooltip="true" align="center" min-width="200"></el-table-column>

    <el-table-column label="网址" header-align="center" prop="website"
                     :show-overflow-tooltip="true" align="center" min-width="200">
      <template slot-scope="scope">
        <el-button type="text" @click="toWebSite(scope.row.website)">{{scope.row.website}}</el-button>
      </template>
    </el-table-column>
  </el-table>
</div>
</template>

<script>
export default {
  name: 'el-table-test',
  data() {
    return {
      key: new Date().getTime(),
      tableData: this.testListData.getListData(),
    };
  },
  methods: {
    toWebSite(website) {
      window.open(`http://${website}`);
    },
  },
};
</script>

<style scoped lang="less">
.content-box {
  text-align: center;
  padding: 0 50px;
}
</style>
