<template>
  <div class="content-box">
    <div class="img-box" ref="refImgDiv" @mousedown="onMouseDown4DivPanel"
         @mousemove="onMouseMove4DivPanel" @mouseup="onMouseUp4DivPanel"
         @mouseout="onMouseOut4DivPanel" :class="{ 'mouse-down': mouseDown }">
      <img src="../assets/img/test_img_drag.jpg" alt="测试图片"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ViewImgDrag',
  data() {
    return {
      isImgPanelDragging: false,
      lastMovePointer: null,
      mouseDown: false,
    };
  },
  methods: {
    /**
     * 获取事件点
     * @param e
     */
    getEventPointer(e) {
      const typeName = e.type.toLowerCase();
      if (typeName === 'touchstart' || typeName === 'touchmove' || typeName === 'touchend') {
        if (e.touches && e.touches.length >= 1) {
          return { x: e.touches[0].clientX, y: e.touches[0].clientY, };
        }
        return null;
      }
      return { x: e.clientX, y: e.clientY, };
    },
    /**
     * 图片块panel鼠标按下
     * @param e
     */
    onMouseDown4DivPanel(e) {
      this.isImgPanelDragging = true;
      this.lastMovePointer = this.getEventPointer(e);
      this.mouseDown = true;
      if (e.preventDefault) {
        e.preventDefault();
      }
    },
    /**
     * 图片块panel鼠标移动
     * @param e
     * @return {boolean}
     */
    onMouseMove4DivPanel(e) {
      if (this.isImgPanelDragging) {
        const p = this.getEventPointer(e);
        if (e.preventBubble) {
          e.preventBubble();
        }
        e.preventDefault(); // 阻止默认行为
        if (e.stopPropagation) {
          e.stopPropagation(); // 阻止事件冒泡
        }
        if (this.lastMovePointer) {
          const moveX = this.lastMovePointer.x - p.x;
          const moveY = this.lastMovePointer.y - p.y;
          this.$refs.refImgDiv.scrollLeft = this.$refs.refImgDiv.scrollLeft + moveX;
          this.$refs.refImgDiv.scrollTop = this.$refs.refImgDiv.scrollTop + moveY;
        }
        this.lastMovePointer = p;
        return false;
      }
      if (e.preventDefault) {
        e.preventDefault();
      }
      return false;
    },
    /**
     * 图片块panel鼠标拿起
     * @param e
     */
    onMouseUp4DivPanel(e) {
      this.isImgPanelDragging = false;
      this.lastMovePointer = null;
      this.mouseDown = false;
      if (e.preventDefault) {
        e.preventDefault();
      }
    },
    /**
     * 图片块panel鼠标移出
     */
    onMouseOut4DivPanel() {
      if (this.isImgPanelDragging) {
        this.isImgPanelDragging = false;
        this.lastMovePointer = null;
        this.mouseDown = false;
      }
    },
  },
};
</script>

<style scoped lang="less">
.content-box {
  text-align: center;
  .img-box {
    display: inline-block;
    overflow: hidden;
    width: 800px;
    height: 100%;
    border: 1px solid #e5e5e5;
    cursor: grab;
  }
  .mouse-down {
    cursor: grabbing;
  }
}
</style>
