<template>
  <div class="page-custom">
    <el-pagination :background="isBackground" layout="total, prev, pager, next, slot"
                   :total="total" :current-page="pageNo"
                   :page-size="pageSize" @current-change="handleCurrentChange">
      <div class="page-jump-box">
        <span>前往</span>
        <div class="input-box">
          <div class="el-input el-input--small">
            <input class="el-input__inner" type="text" v-model="currentInput"
                   @input="onInput(currentInput, totalPageCount)"
                   @keyup.enter="handleCurrentChange(currentInput)"/>
          </div>
        </div>
        <span>页</span>
      </div>
    </el-pagination>
  </div>
</template>

<script>
export default {
  // 自定义分页组件
  name: 'DefinePagination',
  props: {
    // 当前页
    pageNo: {
      type: Number,
      default: 1,
    },
    // 一页显示数量
    pageSize: {
      type: Number,
      default: 10,
    },
    // 总数量
    total: Number,
    // 总页数
    totalPageCount: Number,
    // 是否显示背景
    isBackground: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      currentInput: '',
    };
  },
  methods: {
    /**
     * 切换当前页码
     * @param pageNo 当前页码
     */
    handleCurrentChange(pageNo) {
      let curr = +pageNo;
      if (curr < 1) {
        curr = 1;
      }
      if (curr > this.total) {
        curr = this.total;
      }
      if (this.pageNo !== +this.currentInput) {
        // 当前页码与输入页码不一致时通知父组件更新
        this.$emit('currentChange', curr);
      }
      this.currentInput = '';
    },
    /**
     * 异步设置页码
     * @param pageNo 当前页码
     */
    setPageNo(pageNo) {
      // 延迟执行(异步)
      this.$nextTick(() => {
        this.currentInput = pageNo;
      });
    },
    /**
     * 输入框输入处理
     * @param input 当前输入值
     * @param totalPage 总页数
     */
    onInput(input, totalPage) {
      const reg = /^\d*$/;
      const score = input;
      const scoreNum = parseFloat(score);
      if (isNaN(parseFloat(score)) || !reg.test(score)) {
        this.setPageNo(`${parseFloat(score) || ''}`);
      } else if (scoreNum > totalPage) {
        this.setPageNo(totalPage.toString());
      }
    },
  },
};
</script>

<style scoped lang="less">
.page-custom {
  .page-jump-box {
    margin-left: 24px;
    display: inline-block;
    .input-box {
      margin: 0 2px;
      width: 50px;
      display: inline-block;
    }
  }
}
</style>
<style lang="less">
  .page-custom {
    span {
      font-weight: 400;
    }
    .page-jump-box {
      .input-box {
        input {
          text-align: center;
          height: 28px;
          line-height: 28px;
        }
      }
    }
  }
</style>
