<template>
  <el-scrollbar>
    <el-menu
      :default-active="$route.path" class="menu-box" mode="vertical"
      background-color="#304156" text-color="#fff" active-text-color="#ffd04b"
      @select="menuSelect" :collapse="isCollapse" :class="{ 'slide-collapse': isCollapse }">
      <el-menu-item v-for="item in slider" :key="item.index" :index="item.index">
        <i :class="item.iconClass"></i>
        <span slot="title">{{item.name}}</span>
      </el-menu-item>
    </el-menu>
  </el-scrollbar>
</template>

<script>
export default {
  name: 'DefineSliderBar',
  data() {
    return {
      isCollapse: false,
    };
  },
  methods: {
    menuSelect(index) {
      this.$emit('routerToIndex', index);
    },
  },
  computed: {
    getIsCollapse() {
      return this.store.state.sliderIsCollapse;
    },
  },
  watch: {
    getIsCollapse(val) {
      this.isCollapse = val;
    },
  },
};
</script>

<style scoped lang="less">
.menu-box {
  /*transition: width .28s;*/
  width: 180px;
  height: 100%;
  position: fixed;
  font-size: 0;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1001;
  overflow: hidden;
  .el-menu-item {
    text-align: left;
    user-select: none;
  }
}
.slide-collapse {
  width: 60px;
}
</style>
