<template>
  <div class="content-box" :key="key">
    <el-table :data="tableData" class="table-box" :row-style="treeTableShowTr"
              size="small" @row-click="rowClick" border>

      <el-table-column label="单位名称" header-align="center" prop="name"
                       width="200" :show-overflow-tooltip="true" align="left">
        <template slot-scope="scope">
          <span v-if="treeTableIconShow(scope.row)">
            <i class="el-tree-node__expand-icon el-icon-caret-right"
               :class="{ expanded: scope.row.expanded }"></i>
            {{ scope.row.name }}
          </span>
          <span v-else>
            <span class="ms-tree-space"></span>{{ scope.row.name }}
          </span>
        </template>
      </el-table-column>

      <el-table-column label="地址" header-align="center" prop="address"
                       :show-overflow-tooltip="true" align="center"></el-table-column>

      <el-table-column label="电话" header-align="center" prop="telephone"
                       :show-overflow-tooltip="true" align="center"></el-table-column>

      <el-table-column label="网址" header-align="center" prop="website"
                       :show-overflow-tooltip="true" align="center"></el-table-column>

      <el-table-column label="操作" header-align="center"
                       :show-overflow-tooltip="true" align="center" width="120">
        <template slot-scope="scope">
          <el-button size="small" type="text" v-show="scope.row.hasChildren"
                     @click.stop="edit">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  // 测试tree-table的实现
  name: 'demo',
  data() {
    return {
      key: new Date().getTime(),
      // 测试数据，必须：id,parentId,hasChildren,expanded
      tableData: this.testListData.getTreeListData(),
    };
  },
  methods: {
    /**
     * 点击行展开/收缩
     * @param row 当前行
     */
    rowClick(row) {
      row.expanded = !row.expanded;
    },
    /**
     * 是否显示当前行
     * @param row 当前行数据
     * @returns {string} 当前行的样式
     */
    treeTableShowTr(row) {
      const parentIndex = this.tableData.findIndex((value) => {
        return value.id === row.row.parentId;
      });
      // 关键代码
      const show = (row.row.parentId ? (this.tableData[parentIndex].expanded && this.tableData[parentIndex]._show) : true);
      row.row._show = show;
      return show ? '' : 'display:none;';
    },
    /**
     * 是否显示展开/收缩图标
     * @param record 当前行数据
     * @returns {boolean} true显示，false不显示
     */
    treeTableIconShow(record) {
      return record.hasChildren;
    },
    /**
     * 编辑
     */
    edit() {
      console.log('edit');
    },
  },
};
</script>

<style scoped lang="less">
.content-box {
  text-align: center;
  .table-box {
    // 该样式根据具体业务来进行设置
    width: 70%;
    margin: 0 auto;
  }
  .ms-tree-space {
    // 设置不显示展开/收缩图标时的左侧间距
    position: relative;
    display: inline-block;
    line-height: 1;
    width: 16px;
    height: 12px;
  }
  .ms-tree-space::before {
    content: "";
  }
  .expanded {
    // 该样式为展开时动画
    transform: rotate(90deg);
  }
}
</style>
<style lang="less">
  .content-box {
    .el-table__row {
      cursor: pointer;
    }
    .el-dropdown-link {
      cursor: pointer;
      color: #409eff;
    }
  }
</style>
