<template>
  <div class="container-box">
    <div class="aside-box">
      <define-slider-bar @routerToIndex="toIndex"></define-slider-bar>
    </div>
    <div class="content-container" :class="{ 'content-collapse': collapseText==='展开'}">
      <div class="header-box">
        <div class="btn-collapse">
          <el-button type="primary" size="small"
                     @click="changeBtn">{{collapseText}}</el-button>
        </div>
        <div class="head-text">{{$route.name}}</div>
      </div>
      <div class="main-box">
        <el-scrollbar>
          <transition name="fade" mode="out-in">
            <router-view/>
          </transition>
        </el-scrollbar>
      </div>
    </div>
  </div>
</template>

<script>

import DefineSliderBar from '@/components/DefineSliderBar';

export default {
  name: 'MainIndex',
  components: {
    DefineSliderBar,
  },
  data() {
    return {
      collapseText: '收缩',
    };
  },
  methods: {
    /**
     * 路由跳转
     * @param index 路由路径
     */
    toIndex(index) {
      this.$router.push({
        path: index,
      });
    },
    /**
     * 展开收缩按钮
     */
    changeBtn() {
      this.collapseText = this.collapseText === '收缩' ? '展开' : '收缩';
      this.store.commit('changeSliderCollapse');
    },
  },
};
</script>

<style scoped lang="less">
.common-style1() {
  height: 60px;
  line-height: 60px;
  background-color: #e5e9f2;
}
.container-box {
  height: 100%;
  position: relative;
  .aside-box {
    background-color: #d3dce6;
    height: 100%;
    position: absolute;
    .aside-content {
    }
  }
  .content-container {
    height: 100%;
    margin-left: 180px;
    transition: margin-left .28s;
    .header-box {
      .common-style1();
      height: 59px;
      background-color: #fff;
      border-bottom: 1px solid #e5e5e5;
      font-weight: 700;
      position: relative;
      .btn-collapse {
        position: absolute;
        line-height: 60px;
        left: 10px;
      }
      .head-text {
        text-align: center;
      }
    }
    .main-box {
      height: calc(100% - 60px);
      padding: 10px 0 10px 10px;
    }
  }
  .content-collapse {
    margin-left: 60px;
  }
}
</style>
<style lang="less">
.el-aside {
  width: 260px;
}
</style>
